.settings {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 25px;
}

.settings-section {
  text-align: center;
}

.settings-section label {
  font-weight: 700;
}

.settings-section button {
  font-size: 1.1rem;
  font-weight: 700;
  border: 1px solid #000;
  background-color: transparent;
  color: #000;
  border-radius: 6px;
  outline: none;
  width: 35px;
  height: 35px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
}

.settings-section button.disable {
  opacity: 0.5;
  cursor: not-allowed;
}

.settings-section span {
  width: 45px;
  height: 45px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #fff;
  border-radius: 50%;
  color: #000;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 1.1rem;
}



